<template>
    <div class="contentManager">
        <leftNav :navTitle="navTitle" :chooseTitle="chooseTitle" @change-title="changeTitle"></leftNav>
        <userManager :chooseTitle="chooseTitle" v-show="chooseTitle==''||chooseTitle==''"></userManager>
        <publishManager :chooseTitle="chooseTitle" v-show="chooseTitle==''||chooseTitle==''||chooseTitle==''||chooseTitle==''"></publishManager>
        <storeManager :chooseTitle="chooseTitle" v-show="chooseTitle==''||chooseTitle==''||chooseTitle==''||chooseTitle==''"></storeManager>
        <announceManager :chooseTitle="chooseTitle" v-show="chooseTitle==''"></announceManager>
        <feedBackManager :chooseTitle="chooseTitle" v-show="chooseTitle==''"></feedBackManager>
    </div>
</template>

<script>
import leftNav from './construct/leftNav.vue'
import userManager from './manager/userManager.vue'
import publishManager from './manager/publishManager.vue'
import storeManager from './manager/storeManager.vue'
import announceManager from './manager/announceManager.vue'
import feedBackManager from './manager/feedBackManager.vue'
export default {
    data(){
        return{
            chooseTitle:"",
            navTitle:[{
                content:"",
                isExtension:false,
                child:["",""]
            },{
                content:"",
                isExtension:false,
                child:["","","",""]
            },{
                content:"",
                isExtension:false,
                child:["","","",""]
            },{
                content:"",
                isExtension:false,
                child:""
            },{
                content:"",
                isExtension:false,
                child:""
            },]
        }
    },
    components:{
        leftNav,
        userManager,
        publishManager,
        storeManager,
        announceManager,
        feedBackManager
    },
    methods:{
        changeTitle(title,type){
            if(type=="parent"){
                if(title.child.length==0){
                    this.chooseTitle=title.content;
                }else{
                    // 
                    title.isExtension=!title.isExtension;
                }
            }else if(type=="child"){
                this.chooseTitle=title
            }
        }
    }
}
</script>

<style lang="less" scoped>
.contentManager{
    display: flex;
}
</style>
