<template>
    <div class="table">
        <div class="tableHandle">
            <div class="tableChange">
                <span :class="{'chooseIndex':changeIndex==0}" @click="()=>{changeIndex=0;isRecycle=false;this.dataType='all'}"></span>
                <span v-show="isGoods&&tableType!=='goods'" :class="{'chooseIndex':changeIndex==1}" @click="()=>{changeIndex=1;isRecycle=false;this.dataType='wait'}"></span>
                <span v-show="isGoods" :class="{'chooseIndex':changeIndex==2}" @click="()=>{changeIndex=2;isRecycle=false;this.dataType='through'}"></span>
                <span :class="{'chooseIndex':changeIndex==3}" @click="getRecycleData"></span>
                <span class="borderSpan">&nbsp;</span>
            </div>
            <div class="dataHandle">
                <button v-show="isGoods&&tableType!=='goods'" @click="check()"></button>
                <button v-show="isGoods" @click="check()"></button>
                <button v-show="isGoods" @click="check()"></button>
                <button v-show="chooseTitle==''" @click="showEdit(0)"></button>
                <button v-show="chooseTitle==''" @click="showEdit(1)"></button>
                <button @click="deleteSeveralData(0)"></button>
                <button @click="deleteSeveralData(1)"></button>
                <button class="deleteCompleted" @click="deleteSeveralData(2)"></button>
            </div>
        </div>
        <div class="tableLabel">
            <div class="inputContainer"></div>
            <div class="serial"></div>
            <div class="handle"></div>
            <div class="id" v-if="!isAnnFed">ID</div>
            <div class="userName" v-if="isMember||isGoods"></div>
            <div class="mobile" v-if="isMember"></div>
            <div class="wechat" v-if="isMember"></div>
            <div class="QQ" v-if="isMember">QQ</div>
            <div class="address" v-if="isMember"></div>
            <div class="goodsType" v-if="isGoods">{{goodsTypeName}}</div>
            <div class="goodsName" v-if="isGoods">{{goodsTypeName}}</div>
            <div class="goodsPrice" v-if="isGoods&&!isSend">{{goodsDescribe}}</div>
            <div class="satisifation" v-if="isFeedBack"></div>
            <div class="time" v-if="isAnnFed||isFeedBack">{{isFeedBack?'':''}}</div>
            <div class="title" v-if="isAnnFed"></div>
        </div>
        <div :class="['tableContent',data.serial%2==0?'evenData':'singleData']" v-for="data in manageData">
            <div class="inputContainer">
                <input type="checkbox" v-model="isChecked[data.serial]">
            </div>
            <div class="serial">{{data.serial}}</div>
            <div class="handle">
                <span class="repeal" @click="isRecycle?deleteOneData(1,data.id):deleteOneData(0,data.id)">{{isRecycle?'':''}}</span>
                <span class="thorough" @click="deleteOneData(2,data.id)"></span>
            </div>
            <div class="id" v-if="!isAnnFed">{{data.id}}</div>
            <div class="userName" v-if="isMember||isGoods">{{data.userName}}</div>
            <div class="mobile" v-if="isMember">{{data.phone}}</div>
            <div class="wechat" v-if="isMember">{{data.weChat}}</div>
            <div class="QQ" v-if="isMember">{{data.qq}}</div>
            <div class="address" v-if="isMember">{{data.address}}</div>
            <div class="goodsType" v-if="isGoods">{{data.goodsTypeName}}</div>
            <div class="goodsName" v-if="isGoods">{{data.goodsName}}</div>
            <div class="goodsPrice" v-if="isGoods&&!isSend">{{data.goodsPrice}}</div>
            <div class="satisifation" v-if="isFeedBack">{{data.fbSatisfaction | satisfactionTurn}}</div>
            <div class="time" v-if="isAnnFed">{{data.time}}</div>
            <div class="title" v-if="isAnnFed">{{data.title}}</div>
        </div>
        <div class="pageTurn">
            <span class="turnPage" @click="turn({currentPage:currentPage-1,type:'manager',...{goodsType,tableType}})"><</span>
            <div class="pageContainer">
                <span :class="{'currentPage':p==currentPage}" v-for="p in page" v-cloak @click="turn({currentPage:p,type:'manager',...{goodsType,tableType}})">{{p}}</span>
            </div>
            <span class="turnPage" @click="turn({currentPage:currentPage+1,type:'manager',...{goodsType,tableType}})">></span>
            <span v-cloak>{{page}}</span>
        </div>
        <newAnn v-if="isEdit" @showEdit="showEdit" :editType="editType" :annChange="annChange"></newAnn>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
import newAnn from './newAnn.vue'
export default {
    data(){
        return{
            changeIndex:0,
            isMember:true, // 
            isGoods:false, // 
            isAnnFed:false, // 
            isSend:false, // 
            goodsTypeName:'',
            isEdit:false,
            isFeedBack:false,
            isRecycle:false,
            tableType:'', // 
            isChecked:{0:false},
            goodsType:'',
            editType:0, // 01
            annChange:null,
            dataType:'all'
        }
    },
    props:["tableLabel","chooseTitle"],
    computed:{
        ...mapGetters('analyse',{
            manageData:'getManageData',
            page:'getPage',
            currentPage:'getCurrentPage'
        })
    },
    mounted(){
        this.titleChange(this.chooseTitle)
    },
    methods:{
        ...mapActions("analyse",["turn","requestManageData","requestRecycleData","deleteData","requestWaitData","requestThroughData"]),
        titleChange(val){
            this.isSend=false;
            this.isMember=false;
            this.isGoods=false;
            this.isAnnFed=false;
            this.isFeedBack=false;
            if(val==''){
                this.isMember=true;
                this.tableType='user';
            }else if(val==''){
                this.tableType='visitor';
            }else if(val.search('')==0||val.search('')==0){
                this.isGoods=true;
                this.goodsTypeName=val.substring(2,4);
                if(this.goodsTypeName==''){
                    this.goodsDescribe=''
                    this.goodsType=0;
                }else if(this.goodsTypeName==''){
                    this.goodsDescribe=''
                    this.goodsType=2;
                }
                else if(this.goodsTypeName==''){
                    this.goodsDescribe=''
                    this.goodsType=3;
                }
                if(val.search('')>0){
                    this.isSend=true;
                    this.goodsType=1;
                }
                if(val.search('')===0){
                    this.tableType='release';
                }else{
                    this.tableType='goods';
                }
            }else if(val==''){
                this.isAnnFed=true;
                this.tableType='announcement';
            }else if(val==''){
                this.tableType='feedback';
                this.isFeedBack=true;
            }
            let currentPage=this.currentPage;
            let tableType=this.tableType;
            let goodsType=this.goodsType;
            this.requestManageData({tableType,currentPage,goodsType})
        },
        showEdit(editType){
            let annChange=null;
            this.editType=editType;
            if(editType===0){
                annChange={};
            }
            if(editType===1){
                for(let i=0;i<this.manageData.length;i++){
                    if(this.isChecked[this.manageData[i].serial]){
                        annChange=this.manageData[i];
                        break;
                    }
                }
                if(annChange===null){
                    alert('')
                    return false;
                }
            }
            this.annChange=annChange;
            this.isEdit=!this.isEdit;
        },
        getRecycleData(){
            this.dataType='recycle';
            this.changeIndex=3;
            this.isRecycle=true;
            let currentPage=this.currentPage;
            let tableType=this.tableType;
            let goodsType=this.goodsType;
            this.requestRecycleData({tableType,currentPage,goodsType});
        },
        deleteSeveralData(type){
            let serialList=Object.keys(this.isChecked);
            let idList=[];
            for(let i=0;i<this.manageData.length;i++){
                if(serialList.includes(String(this.manageData[i].serial))){
                    idList.push(this.manageData[i].id);
                }
            }
            this.deleteData({
                tableType:this.tableType,
                id:idList,
                type,
                goodsType:this.goodsType,
                currentPage:this.currentPage,
                dataType:this.dataType
            });
        },
        deleteOneData(type,id){
            this.deleteData({
                tableType:this.tableType,
                id:[id],
                type,
                goodsType:this.goodsType,
                currentPage:this.currentPage,
                dataType:this.dataType
            })
        }
    },
    watch:{
        chooseTitle(newVal){
            this.titleChange(newVal)
        },
        dataType(newVal){
            let data={
                tableType:this.tableType,
                goodsType:this.goodsType,
                currentPage:this.currentPage
            }
            if(newVal==='all')
                this.titleChange(this.chooseTitle)
            else if(newVal==='wait')
                this.requestWaitData(data)
            else if(newVal==='through')
                this.requestThroughData(data)
        },
        currentPage(newVal){
            let data={
                tableType:this.tableType,
                goodsType:this.goodsType,
                currentPage:this.currentPage
            }
            if(this.dataType==='all')
                this.titleChange(this.chooseTitle)
            else if(this.dataType==='recycle')
                this.getRecycleData()
            else if(this.dataType==='wait')
                this.requestWaitData(data)
            else if(this.dataType==='through')
                this.requestThroughData(data)
        },
        manageData(newVal){
            console.log('newVal: ', newVal);
        }
    },
    components:{
        newAnn
    },
    filters:{
        satisfactionTurn:function(val){
            if(val==0){
                return ''
            }else if(val==1){
                return ''
            }else if(val==2){
                return ''
            }else if(val==3){
                return ''
            }else if(val==4){
                return ''
            }
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/less/define.less';
.table{
    width: 1660px;
    height: 790px;
    margin-left: 10px;
    background-color: #fff;
    .tableHandle{
        display: flex;
        justify-content: space-between;
        .tableChange{
            padding: 20px 80px 20px 10px;
            color: #666;
            &>span{
                padding: 9px 18px;
                border-bottom:2px solid @topicShallowBColor;
                box-sizing: border-box;
                cursor: pointer;
            }
            .chooseIndex{
                border: 2px solid @topicShallowBColor;
                border-bottom-color: transparent;
                color:@topicShallowBColor;
            }
            .borderSpan{
                display: inline-block;
                width: 400px;
            }
        }
        .dataHandle{
            padding:20px 60px;
            button{
                margin-right: 20px;
                padding: 10px 19px;
                border: 1px solid #999;
                border-radius: 2px;
                font-size: 18px;
                color: #666;
                background-color: #f2f2f2;
                cursor: pointer;
                &:hover{
                    border-color: @topicShallowBColor;
                    color: #fff;
                    background-color: @topicShallowBColor;
                }
            }
            .deleteCompleted:hover{
                border-color: @topicDeepRColor;
                color: #fff;
                background-color: @topicDeepRColor;
            }
        }
    }
    .tableLabel{
        width: 1660px;
        height: 50px;
        line-height: 50px;
        background-color: @topicShallowBColor;
        color: #fff;
        div{
            display: inline-block;
            text-align: center;
        }
    }
    .tableContent{
        height: 50px;
        font-size: 18px;
        line-height: 50px;
        color: #666;
        input[type='checkbox']{
            margin: 0px 20px 0px 40px;
        }
        div{
            display: inline-block;
            text-align: center;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
        }
    }
    .inputContainer{
        width: 70px;
        input{
            width: 20px;
            height: 20px;
            transform: translateY(3px);
        }
    }
    .serial{
        width: 66px;
    }
    .handle{
        width: 216px;
        span{
            display: inline-block;
            width: 90px;
            cursor: pointer;
        }
        .repeal{
            color: @topicShallowBColor;
        }
        .thorough{
            color:@topicDeepRColor;
        }
    }
    .id{
        width: 165px;
    }
    .userName{
        width: 120px;
    }
    .mobile{
        width: 190px;
    }
    .wechat{
        width: 200px;
    }
    .QQ{
        width: 180px;
    }
    .address{
        width: 320px;
    }
    .goodsType{
        width: 150px;
    }
    .goodsName{
        width: 450px;
    }
    .goodsPrice{
        width: 180px;
    }
    .time{
        width: 160px;
    }
    .title{
        width: 640px;
    }
    .satisifation{
        width: 150px;
    }
    .singleData{
        background-color: #eef1f5;
    }
    .evenData{
        background-color: #e1e7fa;
    }
    .pageTurn{
        @letterSpacing:9px;
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 50px;
        letter-spacing: @letterSpacing;
        font-size: 24px;
        color: @shallowFontColor;
        transform: translatex(@letterSpacing);
        .turnPage{
            cursor: pointer;
        }
        .pageContainer{
            display: flex;
            flex-direction: row;
            min-width:50px; 
            justify-content: space-evenly;
            span{
                display: inline-block;
                width: 15px;
                padding: 0px 10px;
                text-align: center;
                cursor: pointer;
            }
            .currentPage{
                background-color: @topicShallowBColor;
                color: #fff;
            }
        }
    }
}
</style>
