<template>
    <div class="newAnn">
        <h1>{{handleType}}</h1>
        <div class="title">
            <span></span>
            <input type="text" v-model="annChange.title">
        </div>
        <div class="content">
            <p></p>
            <textarea v-model="annChange.content"></textarea>
        </div>
        <button @click="publishAnn"></button>
        <button @click="$emit('showEdit')"></button>
        <p :class="['publishTip',isShowTip?'showTip':'']">{{tipContent}}</p>
    </div>
</template>

<script>
import axios from 'axios'
import Qs from 'qs'
import { setTimeout } from 'timers';
export default {
    data(){
        return{
            isShowTip:false,
            tipContent:'',
            publishTimer:null,
            handleType:'',
            allowChange:true
        }
    },
    props:["annChange","editType"],
    methods:{
        publishAnn(){
            let that=this;
                if(this.annTitle!==''&&this.annContent!==''){
                    if(this.allowChange){
                        this.allowChange=false;
                        // 
                        this.publishTimer=setTimeout(()=>{
                            that.allowChange=true;
                            let url,method;
                            if(that.editType===0){
                                url='/background/announcement/insert'
                                method='post'
                            }else{
                                url='/background/announcement/update'
                                method='put'
                            }
                            // axios({
                            //     url,
                            //     method,
                            //     data:{
                            //         title:that.annTitle,
                            //         content:that.annContent
                            //     },
                            //     transformRequest: [function (data) {
                            //         // data
                            //         return Qs.stringify(data)
                            //     }],
                            //     headers: {
                            //         'deviceCode': 'A95ZEF1-47B5-AC90BF3'
                            //     }
                            // })
                            axios[method](`${url}?title=${title}&content=${content}`)
                            .then(res=>{
                                let tipWord;
                                if(that.editType===0){
                                    tipWord=''
                                }else{
                                    tipWord=''
                                }
                                if(res.data.msg==''||res.data.msg=='')
                                    that.tipContent=`${tipWord}`
                                else
                                    that.tipContent=`${tipWord}`
                                that.isShowTip=true;
                                setTimeout(()=>{
                                    that.isShowTip=false;
                                },3000)
                            }).catch(err=>{
                                console.log(`the err happen when push Ann because ${err}`)
                            })
                        },3000)
                    }else{
                        this.tipContent='';
                        this.isShowTip=true;
                        setTimeout(()=>{
                            this.isShowTip=false;
                        },3000)
                    }
            }else{
                this.tipContent='';
                this.isShowTip=true;
                setTimeout(()=>{
                    this.isShowTip=false;
                },3000)
            }
        }
    },
    filters:{
        changeTItle(val){
            if(val===0){
                return ''
            }else{
                return ''
            }
        }
    },
    mounted(){
        if(this.editType===0){
            this.annChange={
                title:'',
                content:''
            }
            this.handleType=''
        }else{
            this.handleType=''
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/less/define.less';
@borderColor:#f2f2f2;
.newAnn{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 1200px;
    height: 800px;
    border: 4px solid @topicShallowBColor;
    box-sizing: border-box;
    text-align: center;
    background-color: #fff;
    h1{
        margin: 30px;
        font-size: 30px;
        color: @topicShallowBColor;
    }
    div{
        width: 1000px;
        margin: 0 auto;
        box-sizing: border-box;
    }
    .title{
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        height: 50px;
        border:2px solid @borderColor;
        text-align: left;
        span{
            padding: 8px 20px;
            font-size: 22px;
            background-color: @borderColor;
            color: @topicShallowBColor;
        }
        input{
            width: 900px;
            height: 46px;
            border: none;
            font-size: 18px;
            outline:none;
        }
    }
    .content{
        margin-bottom: 10px;
        border: 8px solid @borderColor;
        text-align: left;
        p{
            padding: 6px 12px;
            font-size: 22px;
            background-color: @borderColor;
            color: @topicShallowBColor;
        }
        textarea{
            width: 980px;
            height: 480px;
            padding: 10px;
            box-sizing: border-box;
            border: none;
            font-size: 18px;
            outline:none;
            resize: none;
        }
    }
    button{
        padding: 14px 47px;
        margin: 0 20px;
        border: 2px solid @topicShallowBColor;
        font-size: 24px;
        color:@topicShallowBColor;
        background-color: #fff;
        cursor: pointer;
        &:hover{
            color:#fff;
            background-color: @topicShallowBColor;
        }
    }
    .publishTip{
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        padding: 20px 0px;
        font-size: 40px;
        background-color:@borderColor;
        color: @topicShallowBColor;
        transform: translateX(-50%) translateY(-50%);
        opacity: 0;
    }
    .showTip{
        animation: showTip 3s 1;
    }
    @keyframes showTip{
        0%,100%{
            opacity: 0;
        }
        50%{
            opacity: 1;
        }
    }
}
</style>
